:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

.container-form {
  width: 100%;
  margin-bottom: 3rem;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
  position: relative;
  font-size: 1rem;
  line-height: 1.6;
  color: #fff;
}

a {
  /* color: var(--primary-color); */
  color: #fff;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utilities */

.container {
  height: auto;
  /* height: 120vh; */
  /* max-width: 1100px; */
  max-width: 1250px;
  /* margin: auto; */
